html {
  font-size: 68.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #000;
}

a {
  color: #081fc4;
  text-decoration: none;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-size: 5rem;
}
@media (max-width: 768px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 3.5rem;
  color: #081fc4;
}

h3 {
  font-size: 3rem;
  color: #081fc4;
}

.header {
  position: relative;
}
.header .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(2, 0, 36);
  background: radial-gradient(circle, rgba(2, 0, 36, 0) 0%, rgba(9, 9, 121, 0.4542191877) 35%, rgba(18, 81, 173, 0.6222864146) 100%);
}
.header .overlay .contenido-header {
  display: flex;
  flex-direction: column;
}
.header .overlay .contenido-header p {
  text-align: center;
  color: #fff;
  font-weight: 400;
  margin: 0;
  font-size: 2.2rem;
}
@media (max-width: 768px) {
  .header .overlay .contenido-header p {
    font-size: 1.8rem;
  }
}
@media (max-width: 480px) {
  .header .overlay .contenido-header p {
    font-size: 1.4rem;
  }
}
.header .overlay .contenido-header .separador {
  display: flex;
  justify-content: center;
  gap: 4rem;
  margin-top: 4rem;
}
@media (max-width: 768px) {
  .header .overlay .contenido-header .separador {
    gap: 1rem;
    font-size: 1.4rem;
  }
}
@media (max-width: 480px) {
  .header .overlay .contenido-header .separador {
    gap: 0.7rem;
    font-size: 1.1rem;
  }
}
.header .overlay .contenido-header .separador a {
  font-weight: 400;
  transition: all 0.3s ease;
}
.header .overlay .contenido-header .separador a:hover {
  text-shadow: 0px 0px 14px rgb(57, 150, 150);
  color: #081fc4;
  transform: scale(1.2);
}
.header .overlay .contenido-header .flex {
  display: grid;
  place-self: center;
  margin-top: 17rem;
}
@media (max-width: 480px) {
  .header .overlay .contenido-header .flex h1 {
    font-size: 3rem;
  }
}
.header .overlay .contenido-header a {
  color: #fff;
  transition: all 0.3s ease;
}
.header .overlay .contenido-header a span {
  font-weight: 200;
}
.header .overlay .contenido-header a:hover {
  text-shadow: 0px 0px 14px rgb(57, 150, 150);
  color: #081fc4;
}
.header .social {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}
@media (max-width: 768px) {
  .header .social p {
    font-size: 1.4rem;
    margin-top: 8rem;
  }
  .header .social i {
    font-size: 5rem;
    margin-bottom: 4rem;
    padding: 2rem;
  }
}
.header .social p {
  color: #fff;
  margin-top: 6rem;
}
.header .social .whats {
  color: #3eba4f;
}
.header .social i {
  color: #385599;
  font-size: 8rem;
  margin-bottom: 4rem;
  padding: 2rem;
  transition: all 0.3s ease;
}
.header .social i:hover {
  transform: scale(1.15);
}
.header .imagen-fondo1 {
  background-image: url("../../../build/img/fondo.webp");
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.titulo-centrado {
  text-align: center;
  margin-top: 5rem;
  font-weight: 200;
}

.nosotros {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem;
  margin-bottom: 5rem;
}
@media (max-width: 768px) {
  .nosotros {
    display: block;
  }
}
@media (max-width: 480px) {
  .nosotros {
    display: block;
  }
}
.nosotros .card-nosotros {
  padding: 2rem;
  text-align: center;
  border-radius: 1.5rem;
  transition: all 0.3s ease;
}
.nosotros .card-nosotros h2 {
  font-weight: 400;
  color: #000;
}
.nosotros .card-nosotros i {
  font-size: 9rem;
  color: #081fc4;
}
.nosotros .card-nosotros:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  transform: scale(1.1);
  background-color: #9da9fb;
}

.mapa {
  margin-bottom: 5rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  border-radius: 2rem;
}
.mapa iframe {
  width: 100%;
  border-radius: 2rem;
}

.imagen-fondo {
  background-image: url("../../../build/img/contactar.webp");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  width: 100%;
  height: 30rem;
}
.imagen-fondo h3 {
  margin-top: 5rem;
  text-align: center;
  color: #000;
  font-weight: 400;
  padding-top: 5rem;
}
@media (max-width: 480px) {
  .imagen-fondo h3 {
    font-size: 2.5rem;
  }
}
.imagen-fondo a {
  color: #57c863;
  text-align: center;
  font-size: 8rem;
  display: flex;
  justify-content: center;
}
@media (max-width: 480px) {
  .imagen-fondo a {
    font-size: 4rem;
  }
}

.niveles-columnas {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 1rem;
  gap: 2rem;
}
.niveles-columnas .contenido-calendario h2 {
  text-align: center;
}
.niveles-columnas .contenido-calendario H3 {
  text-align: center;
  font-weight: 200;
  color: #000;
}
@media (max-width: 768px) {
  .niveles-columnas .contenido-calendario H3 {
    font-size: 2rem;
  }
}
@media (max-width: 480px) {
  .niveles-columnas .contenido-calendario H3 {
    font-size: 1.8rem;
  }
}
.niveles-columnas .contenido-calendario p {
  text-align: center;
  font-weight: 300;
}
@media (max-width: 768px) {
  .niveles-columnas .contenido-calendario p {
    font-size: 1.6rem;
  }
}
@media (max-width: 480px) {
  .niveles-columnas .contenido-calendario p {
    font-size: 1.4rem;
  }
}
.niveles-columnas .imagen-nivel1 {
  background-image: url("../../../build/img/intro.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.niveles-columnas .imagen-nivel2 {
  background-image: url("../../../build/img/eip3.webp");
  background-repeat: no-repeat;
  background-size: cover;
}
.niveles-columnas .imagen-nivel3 {
  background-image: url("../../../build/img/gap3.webp");
  background-repeat: no-repeat;
  background-size: auto;
  background-position: center center;
}

.calendario-contenedor {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}
@media (max-width: 768px) {
  .calendario-contenedor {
    display: block;
    margin-bottom: 3rem;
  }
}
@media (max-width: 480px) {
  .calendario-contenedor {
    display: block;
    margin-bottom: 3rem;
  }
}
.calendario-contenedor ul {
  padding-left: 0;
}
.calendario-contenedor ul li {
  list-style: none;
  margin-top: 1rem;
  padding: 0;
  color: #fff;
}
.calendario-contenedor .calendario {
  transition: all 0.3s ease;
}
@media (max-width: 768px) {
  .calendario-contenedor .calendario {
    margin-bottom: 3rem;
  }
}
@media (max-width: 768px) {
  .calendario-contenedor .calendario {
    margin-bottom: 3rem;
  }
}
.calendario-contenedor .calendario p {
  color: #fff;
  font-weight: 200;
}
.calendario-contenedor .calendario:hover {
  transform: scale(1.1);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}
.calendario-contenedor .intro {
  background-color: #081fc4;
  padding: 5rem;
  border-radius: 15px;
}
.calendario-contenedor .eip {
  background-color: hsla(0deg, 100%, 50%, 0.664);
  padding: 5rem;
  border-radius: 15px;
}
.calendario-contenedor .gap {
  background-color: #0772ff;
  padding: 5rem;
  border-radius: 15px;
}
.calendario-contenedor p {
  text-align: center;
}

.fecha h2 {
  text-align: center;
}
.fecha h3 {
  font-size: 3.5rem;
}
.fecha p {
  text-align: center;
}
.fecha .bg {
  background-image: url("../../../build/img/velero.webp");
  background-position: center center;
  background-size: cover;
  min-height: 30rem;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}
@media (max-width: 768px) {
  .fecha .bg {
    display: block;
    padding: 1rem 0;
  }
}
.fecha .panel {
  background-image: linear-gradient(to bottom right, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0.1) 100%);
  backdrop-filter: blur(6px);
  border-radius: 12px;
  width: 320px;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  padding: 60px;
  box-shadow: rgba(255, 255, 255, 0.5) -20px -20px 45px inset, rgba(0, 0, 0, 0.1) 10px 10px 20px, rgba(0, 0, 0, 0.06) 5px 5px 10px;
  position: relative;
  margin: 2rem;
  transition: all 0.3s ease;
}
@media (max-width: 768px) {
  .fecha .panel {
    width: 90%;
  }
}
.fecha .panel:hover {
  transform: scale(1.05);
}
.fecha .panel::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.05) 40%, rgba(255, 255, 255, 0) 40%);
  pointer-events: none;
}
.fecha h2 {
  font-size: 2em;
  font-weight: 1000;
  margin-top: 0;
}
.fecha .card__text {
  font-size: 14px;
  line-height: 1.45;
  opacity: 0.8;
  margin-bottom: 2em;
}
.fecha .button {
  display: inline-block;
  padding: 1.5em 3em;
  background-image: linear-gradient(-45deg, rgb(9, 28, 60) 0%, rgb(67, 46, 103) 100%);
  text-decoration: none;
  font-weight: 900;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 0.2em;
  border-radius: 10em;
  color: white;
}

.footer {
  background-color: #3a74c1;
  height: 15rem;
}
.footer .contenido-footer {
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.footer .contenido-footer p {
  color: #fff;
  margin: 0;
  font-size: 1.5rem;
  font-weight: 200;
}
@media (max-width: 768px) {
  .footer .contenido-footer p {
    font-size: 1.2rem;
  }
}
.footer .separador {
  display: flex;
  justify-content: center;
  gap: 4rem;
  margin-top: 4rem;
}
@media (max-width: 768px) {
  .footer .separador {
    gap: 1rem;
    font-size: 1.4rem;
  }
}
@media (max-width: 480px) {
  .footer .separador {
    gap: 0.7rem;
    font-size: 1.1rem;
  }
}
.footer .separador a {
  color: #fff;
  font-weight: 400;
  transition: all 0.3s ease;
}
.footer .separador a:hover {
  text-shadow: 0px 0px 14px rgb(57, 150, 150);
  color: #081fc4;
  transform: scale(1.2);
}

.galeria {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}
@media (max-width: 768px) {
  .galeria {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 768px) {
  .galeria {
    grid-template-columns: repeat(1, 1fr);
  }
}
.galeria .imagen-galeria {
  width: 100%;
}

.boton {
  display: flex;
  justify-content: center;
  margin: 4rem 0;
}
.boton .boton-galeria {
  background-color: #081fc4;
  margin: 0 auto;
  padding: 2rem;
  border-radius: 20px;
  color: #fff;
  font-weight: 200;
  transition: all 0.3s;
  box-shadow: rgba(75, 46, 240, 0.4) 5px 5px, rgba(49, 46, 240, 0.3) 10px 10px, rgba(49, 46, 240, 0.2) 15px 15px, rgba(46, 65, 240, 0.1) 20px 20px, rgba(46, 59, 240, 0.05) 25px 25px;
}
.boton .boton-galeria:hover {
  background-color: #041062;
  transform: scale(1.05);
}

.galeria-pag {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 2rem;
  margin-bottom: 4rem;
}
@media (max-width: 768px) {
  .galeria-pag {
    grid-template-columns: repeat(1, 1fr);
  }
}
.galeria-pag .imagen-galeria {
  width: 100%;
}

.galeria-social p {
  text-align: center;
}
.galeria-social .iconos-galeria {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.galeria-social .iconos-galeria a {
  font-size: 7rem;
  transition: all 0.3s;
}
.galeria-social .iconos-galeria a:hover {
  transform: scale(1.15);
}
.galeria-social .iconos-galeria a .whats {
  color: #3eba4f;
}

.testimonios {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (max-width: 768px) {
  .testimonios {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (max-width: 480px) {
  .testimonios {
    grid-template-columns: repeat(1, 1fr);
  }
}
.testimonios .testimonio-contenido {
  backdrop-filter: blur(6px);
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #455bf7;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
  padding: 2rem;
  transition: all 0.3s;
}
.testimonios .testimonio-contenido .imagen-testimonio-diego {
  background-image: url("../../../build/img/diego.webp");
  background-position: center center;
  background-size: cover;
  width: 10rem;
  height: 10rem;
  margin: 2rem 0;
  border-radius: 50%;
}
.testimonios .testimonio-contenido .imagen-testimonio-gabriel {
  background-image: url("../../../build/img/gabriel.webp");
  background-position: center center;
  background-size: cover;
  width: 10rem;
  height: 10rem;
  margin: 2rem 0;
  border-radius: 50%;
}
.testimonios .testimonio-contenido:hover {
  transform: scale(1.05);
  background-color: #0923dd;
}
.testimonios .testimonio-contenido p {
